$default-font: "OpenSans", "Arial", sans-serif;

$mobile-width: 320px;
$tablet-width: 660px;
$desktop-width: 960px;

$mobile-padding: 20px;
$tablet-padding: 20px;
$desktop-padding: 10px;

$retina-dpi: 144dpi;
$retina-dppx: 1.5dppx;

@keyframes bounce {
  0% {
    transform: translateY(-2000px);
  }

  70% {
    transform: translateY(30px);
  }

  90% {
    transform: translateY(-10px);
  }

  100% {
    transform: translateY(0);
  }
}

@mixin wrapper() {
  margin: 0 auto;
  @media (min-width: $mobile-width) {
    width: calc(#{$mobile-width} - #{$mobile-padding} * 2);
    padding-right: $mobile-padding;
    padding-left: $mobile-padding;
  }
  @media (min-width: $tablet-width) {
    width: calc(#{$tablet-width} - #{$tablet-padding} * 2);
    padding-right: $tablet-padding;
    padding-left: $tablet-padding;
  }
  @media (min-width: $desktop-width) {
    width: calc(#{$desktop-width} - #{$desktop-padding} * 2);
    padding-right: $desktop-padding;
    padding-left: $desktop-padding;
  }
}

@mixin fieldset-border() {
  border: 2px solid $border-color;
}

@mixin toggles() {
  display: block;
  position: relative;
  padding: 0;
  margin: 0 10px;
  width: 6px;
  height: 6px;
  background-color: transparent;
  border: none;

  &::before {
    content: "";
    display: block;
    position: absolute;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    width: 6px;
    height: 6px;
    border: 3px solid rgba($blue--dark, 0.3);
    border-radius: 50%;
    background-color: $white;
  }

  &:hover::before {
    border-color: rgba($blue--dark, 0.6);
  }

  &:active::before {
    border-color: rgba($blue--dark, 0.1);
  }

  &--active::before {
    width: 12px;
    height: 12px;
    margin-left: -2px;
    margin-top: -2px;
    border-color: $blue--dark;
  }

  &--active:hover::before,
  &--active:active::before {
    border-color: $blue--dark;
  }
}

$white: #ffffff;
$black: #000000;
$blue--dark: #283645;
$blue--dark-sec: #1d2631;
$grey: #e5e5e5;
$grey--light: #f2f2f2;
$pink: #d22856;
$green: #2aab6d;
$green--dark: #1c9b5e;
$green-leafy: #3dc644;
$orange: #f25b26;

$background-color-header--opened: $blue--dark;
$background-color-header--closed: rgba($black, 0.3);
$background-color-intro: $blue--dark-sec;
$background-color-download: $background-color-header--opened;
$background-color-main: $white;
$background-color-main--additional: $grey--light;
$background-color-table-header: $blue--dark;

$border-color: $grey;

$button-color--hover: $white;
$button-color--active: $white;
$button-color--green: $green;
$button-color--green-hover: $green--dark;
$button-color--green-active: $green--dark;
$button-color--green-disabled: $grey;
$button-text-color: $white;
$button-text-color--hover: $pink;
$button-text-color--active: rgba($pink, 0.3);
$button-text-color--green: $white;
$button-text-color--green-hover: $white;
$button-text-color--green-active: rgba($white, 0.3);
$button-text-color--green-disabled: $white;
$button-post-border: $pink;
$button-post-text: $pink;
$button-post-text--hover: $white;
$button-post-background--hover: $pink;
$button-post-text--active: rgba($white, 0.3);
$button-post-background--active: $pink;
$button-reset-border: rgba($blue--dark, 0.1);
$button-reset-border--hover: rgba($blue--dark, 0.3);
$button-reset-border--active: rgba($blue--dark, 0.3);
$button-reset-text: rgba($blue--dark, 0.1);
$button-reset-text--hover: rgba($blue--dark, 0.3);
$button-reset-text--active: rgba($white, 0.3);
$button-reset-background: transparent;
$button-reset-background--hover: transparent;
$button-reset-background--active: rgba($blue--dark, 0.3);

$text-color: $blue--dark;
$text-color--header: $white;
$text-color--additional: $pink;

$heading-color: $black;

$menu-mobile-color: $white;
$menu-mobile-color--hover: $pink;
$menu-mobile-color--active: rgba($pink, 0.3);
$menu-desktop-color: $white;
$menu-desktop-color--hover: $pink;
$menu-desktop-color--active: rgba($white, 0.3);
